.headerMain{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    width: 100%;
}
.space{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.menu{
    align-items: center;
    width: 80%;
    height: 32px;
    border-top: 1px solid rgba(216, 212, 212, 0.833);
    border-bottom: 1px solid rgba(216, 212, 212, 0.833);
    border-left: none;
    border-right: none;
    display: flex;
    box-sizing: border-box;
    box-shadow: 0 2px 4px rgba(141, 139, 139, 0.1);
    justify-content: space-between;
}
.menu img{
    height: 20px;
     margin-left: 10px;
}
.CountryText {
    font-size: 12px;
    font-weight: bold;
    color: rgba(56, 56, 56, 0.704);
    margin-left: 10px;
}
.Country {
    font-size: 12px;
    border: none;
    width: 8vh; 
    cursor: pointer;
    transition: background-color 0.3s ease, border-color 0.3s ease; 
}
.Country option {
    font-size: 12px;
    cursor: pointer;
}
.Country option img{
    height: 15px;
}
.line{
    height: 22px;
    width: 1px;
    background-color:  rgba(184, 182, 182, 0.784);
    box-shadow: 0 2px 4px rgba(141, 139, 139, 0.1);
    margin-left: 10px;
}
.CurrentDate{
    margin-left: 10PX;
    font-size: 12px;
    font-weight: bold;
    color: rgba(56, 56, 56, 0.704);
}
.Subscribe button{
    height: 27px;
    width: 15vmin;
    background-color: rgb(178, 31, 31);
    color: white;
    border-radius: 1cap;
    font-size: 12px;
    font-weight: bold;
}
.Sign button{
    height: 27px;
    width: 12vmin;
    border-radius: 1cap;
    color: red;
    border: 1px solid red;
    font-size: 12px;
    font-weight: bold;
    background-color: transparent;
}
.button {
    display: flex;
    align-items: center;
    gap: 10px; 
    margin-left: auto; 
  }
 
.newsname label{
    font-size: 45PX;
    font-weight:550;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.newsname{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 14vmin;
}
.itemAll{
    display: flex;
    justify-content: center;
    align-items: center; 
    width: 100%;
}
.itemSet{
   width: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
}
.itemst1{
    display: flex;
    width: 80%;
}
.item {
    align-items: center;
    width: 100%;
    height: 32px;
    border-top: 1px solid rgba(216, 212, 212, 0.833);
    border-left: none;
    border-right: none;
    display: flex;
    box-sizing: border-box;
    box-shadow: 0 2px 4px rgba(141, 139, 139, 0.1);
    background-color: #f6f6f6;
    gap: 10px;
    border-bottom: 2px solid rgb(44, 44, 96)
}

.buttonsear{
    background-color: transparent;
    height: 34px;
    border: none;
    font-size: 12px;
}
.search{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: 0px ; 
    border-top: 1px solid rgba(216, 212, 212, 0.833);
    box-shadow: 0 2px 4px rgba(141, 139, 139, 0.1);
    background-color: #f6f6f6;
    border-bottom: 2px solid rgb(44, 44, 96);
    box-sizing: border-box;
    width: 40px;
}
.search img{
    cursor: pointer;
    height: 20px;
}
.setline{
    display: flex;
    justify-content: center;
    align-items: center;
}
.line1{
    height: 2px;
    width: 80%;
    background-color: rgb(44, 44, 96);
}
.trending{
    height: 30px;
}
.latest{
    font-size: 13px;
    font-weight: 600;
    color: rgba(56, 56, 56, 0.704);
}
/* Header.css */

.transition-container {
    opacity: 0;
    transform: translateY(-10px);
    transition: opacity 0.5s ease, transform 0.5s ease;
    max-height: 0; 
    overflow: hidden; 
}

.transition-container.show {
    opacity: 1;
    transform: translateY(0);
    max-height: 500px; 
}

.logo{
    background-color:rgba(195, 188, 188, 0.83);
    border: none;
    color: rgba(202, 26, 26, 0.749);
    font-size: 18px;
    font-weight: 700;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    align-items: center;
    gap: 10px;
    border-bottom: 2px solid rgb(44, 44, 96)
}
.logo img{
    height: 20px;
    color: red;
}

@media (max-width: 1025px) {
    .itemst1 {
        display: flex;
        width: 80%;
    }
    .itemSet::-webkit-scrollbar {
        display: none; 
    }
    .item {
        display: flex; 
        align-items: center; 
        overflow-y: scroll; 
        scrollbar-width: none; 
        -ms-overflow-style: none;  
        background-color: #f4f4f4;
    }

    .buttonsear {
        background-color: transparent;
        height: 34px;
        border: none;
    }
    .logo {
        border-bottom: 2px solid rgb(44, 44, 96);
        display: flex;
        align-items: center;
    }

    .logo img {
        object-fit: contain;
    }

    .item button {
        display: block;
        margin: 5px 0;
        cursor: pointer;
        flex-grow: 1; 
    }

    .item button:hover {
        background-color: #ccc;
    }



}
@media (max-width: 910px) {

    .Subscribe button{
        height: 0;
        width: 0;
        font-size: 0;
        border: none;
        background-color: transparent;
        opacity: 0;
    }
}
@media (max-width: 637px) {
    .newsname label{
        font-size: 40PX;
    }
}
@media (max-width: 560px) {
    .newsname label{
        font-size: 35PX;
    }
}
@media (max-width: 494px) {
    .newsname label{
        font-size: 30PX;
    }
    .Sign button{
        width: 13vmin;
        font-size: 10px;
    }
    .CountryText {
        font-size: 0px;
    }
    .Country {
        font-size: 11px;
        border: none;
    }
    .CurrentDate{
        font-size: 10px;
    }
}
@media (max-width: 426px) {
    .newsname label{
        font-size: 28PX;
    }
    .CurrentDate{
        font-size: 9px;
    }
}
@media (max-width: 394px) {
    .newsname label{
        font-size: 26PX;
    }
    .Sign button{
        width: 12vmin;
        font-size: 8px;
    }
    .CountryText {
        font-size: 0px;
        margin: 0;
    }
    .CurrentDate{
        font-size: 8px;
    }
}
@media (max-width: 364px) {
    .newsname label{
        font-size: 24PX;
    }
    .Sign button{
        width: 13vmin;
        font-size: 8px;
    }
}
