.topall{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.topset{
  width: 80%;
  display: flex;
}
.setheadlinetext{
  width: 80%;
}
.HeadText{
display: flex;
align-items: center;
justify-content: center;
width: 100%;
}
.HeadTestset{
width: 80%;
margin: 0;
}
.card-title{
font-size: 18px;
font-weight: 600;
font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
margin-left: 10px;
}
.sportstest{
font-size: 0px;
}
.card-body p{
  font-size: 14px;
}
.moviestext{
  font-size: 0px;
  margin-left: 0px;
  }
.Headsmall{
display: flex;
justify-content: space-between;
width: 96%;
margin:0px;
}
.Headsmall h3{
  margin: 0px;
}
.topheadlinetext{
font-size: 0px;
opacity: 0;
}
.top{
  width: 50%;
  height: 50vmin;
  cursor: pointer;
  background-color: #f0f0f0;
  border-radius: 1cap;
  transition: transform 0.3s ease;
  margin-bottom: 10px;
}
.top img{
 width: 100%;
 height: 100%;
 object-fit: cover;
 display: block;
 transition: transform 0.3s ease;
}
.toptext{
  color: black;
  font-weight: bold;
  margin-top: 30px;
}
.top-image:hover {
  transform: scale(0.95);
} 
.image-title {
  font-weight: bold;
  font-size: 3.7vmin;
}
.card{
  width: 35vmin;
  height: 50vmin;
  cursor: pointer;
  transition: transform 0.3s ease;
  margin: 0;
}
.card:hover img {
  transform: scale(1.05); 
}
.card img {
  width: 100%;
  height: 40%;
  transition: transform 0.3s ease; 
}
.line4{
  height: 50vmin;
  width: 1px;
  background-color: rgba(184, 182, 182, 0.784);
  box-shadow: 0 2px 4px rgba(141, 139, 139, 0.1);
  margin-left: 20px;
  margin-right: 20px;
}
.closeitemall{
  display: flex;
  align-items: center;
  justify-content: center;
}
.closeitemset{
  width: 80%;
  margin-top: 7vmin;
}
.colseitem {
  border: none;
  border-top: 1px dotted black;
  position: relative;
  width: 100%;
  height: 1px;
}

.colseitem::before {
  content: '';
  position: absolute;
  left: 0;
  top: -2px;
  width: 3px;
  height: 3px;
  background-color: rgba(77, 76, 76, 0.67);
  border-radius: 50%;
}

.colseitem::after {
  content: '';
  position: absolute;
  right: 0;
  top: -2px;
  width: 3px;
  height: 3px;
  background-color: rgba(77, 76, 76, 0.67);
  border-radius: 50%;
}
.colseitem + .colseitem {
  margin-top: 1.5px;
}
